body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#root{display:flex;justify-content:center;align-items:center;width:100%;height:100%;min-height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.image-overlay{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);max-width:60px;max-height:60px}

.camera-container{position:relative;width:100%;height:100%}#video{max-width:100%;max-height:100%}

